.header {
  width: 100%;  
  padding-top: 45px;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;    
}

.header .container {
  width: 100%;
  background-color: rgb(230, 241, 245);
  display: flex;
  position: fixed;  
  z-index: 9;
}

.header .categories {
  width: 60%;
  list-style: none;  
  display: flex;  
  align-items: center;  
  justify-content: space-between;
}

.header .category {
  font-size: 18px;
  font-weight: 500;
}

.header .logo {
  width: 80px;
  height: 65px;
  margin: 25px 150px;
}