.footer {
  background-color: #272727;
  border-top: 1px solid #333;  
  /* margin-top: 50px; */
}

.footer .inner {
  padding: 30px 0 20px 0;
}

.footer .info {  
  text-align: center;
}

.footer .info span {
  margin-right: 20px;
  color: #999;
  font-size: 12px;
}

.footer .info span:last-child {
  margin-right: 0;
}

.footer .copyright {
  color: #999;
  font-size: 12px;
  text-align: center;
  margin-top: 12px;
}

.footer .logo {
  width: 130px;
  height: 60px;
  margin: 30px auto 0;
  display: flex;
  justify-content: center;
}